.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: cubic-bezier(0.075, 0.82, 0.165, 1) ;
  }
}

.bar {
  display: flex;
  min-height: 20vh;
  flex-direction: column ;
  
  background-color: #282c34;
  
  font-size: calc(01px + 1.5vmin);
  color: white;
  text-align: center;

}

.center{
  align-items: center;
}


.App-head {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  
  
  
  font-size: calc(01px + 2vmin);
  color: rgb(0, 0, 0);
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 99vh;
  display: flex;
  flex-direction: column;
  
  
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Head{

  background-color: bisque;
  color: black;
  align-self: center;
  text-size-adjust: inherit;
  text-shadow: 2px;
}

.body
{
text-size-adjust: inherit;
}